.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.app-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap-reverse;
  height: 100vh;
  background-size: cover;
  /* background: linear-gradient(
    rgba(0, 0, 0, 0.5), 
    rgba(0, 0, 0, .5)),
    url('./photos/deepSpace.jpeg'); */
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.logo {
  width: 90%;
}

.two-column {
  width: 50%;
  min-width: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.myVideo {
  position: fixed;
  right: 0;
  bottom: 0;
  min-width: 100%; 
  min-height: 100%;
  z-index: -1;
}